import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './ManageCategories.css';

const ManageCategories = () => {
    const [categories, setCategories] = useState([]);

    useEffect(() => {
        // Kategrik lekrdezse az API-bl
        const fetchCategories = async () => {
            try {
                const response = await fetch('http://localhost:5000/api/categories');
                const data = await response.json();
                setCategories(data);
            } catch (error) {
                console.error('Hiba a kategrik lekrsekor:', error);
            }
        };

        fetchCategories();
    }, []);

    const handleDelete = async (id) => {
        const confirmDelete = window.confirm('Biztosan trlni szeretnd ezt a kategrit?');
        if (!confirmDelete) return;
    
        try {
            const response = await fetch(`http://localhost:5000/api/categories/${id}`, {
                method: 'DELETE',
            });
            if (response.ok) {
                setCategories(categories.filter((category) => category._id !== id));
                alert('Kategria sikeresen trlve!');
            } else {
                alert('Hiba trtnt a kategria trlsekor.');
            }
        } catch (error) {
            console.error('Hiba a kategria trlsekor:', error);
        }
    };

    return (
        <div className="manage-categories">
            <h1>Kategrik Kezelse</h1>
            <ul className="category-list">
                {categories.map((category) => (
                    <li key={category._id} className="category-item">
                        <p><strong>{category.name}</strong></p>
                        <div className="category-actions">
                            <Link to={`/admin/edit-category/${category._id}`} className="edit-button">Szerkeszts</Link>
                            <button onClick={() => handleDelete(category._id)} className="delete-button">Trls</button>
                        </div>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default ManageCategories;
