import React from 'react';
import { useCart } from '../context/CartContext';
import './ReviewOrder.css';

const ReviewOrder = ({ onBack, orderData }) => {
    const { clearCart } = useCart(); // Kosr kirtsnek fggvnye

    
    const handleSubmit = async () => {
        console.log(orderData);
        try {
            const response = await fetch('http://localhost:5000/api/orders', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}` // Token hozzadsa
                },
                body: JSON.stringify(orderData),
            });
    
            const result = await response.json();
    
            if (response.ok) {
                alert('Rendels sikeresen vglegestve!');
                clearCart(); // Kosr kirtse a rendels sikeres leadsa utn
            } else {
                alert(`Hiba trtnt a rendels vglegestse sorn: ${result.message}`);
            }
        } catch (error) {
            alert(`Hiba trtnt: ${error.message}`);
        }
    };
    
    

    return (
        <div className="review-order-container">
            <h2>Adatok ellenrzse</h2>
            <div className="order-summary">
                <h3>Szmlzsi adatok:</h3>
                <p><strong>Email:</strong> {orderData.contactInfo?.email || 'Nincs megadva'}</p>
                <p><strong>Nv:</strong> {orderData.contactInfo?.name || 'Nincs megadva'}</p>
                <p><strong>Cm:</strong> {orderData.contactInfo?.address || 'Nincs megadva'}, {orderData.contactInfo?.city || ''}, {orderData.contactInfo?.postalCode || ''}</p>
                <p><strong>Telefonszm:</strong> {orderData.contactInfo?.phone || 'Nincs megadva'}</p>
                
                <h3>Szlltsi s fizetsi md:</h3>
                <strong>Szlltsi md:</strong> {orderData.shippingMethod?.method || 'Nincs kivlasztva'} - {orderData.shippingMethod?.price?.toLocaleString()} Ft
                {orderData.shippingMethod.deliveryDetails && (
                    <>
                        <p><strong>Szlltsi nv:</strong> {orderData.shippingMethod.deliveryDetails.name}</p>
                        <p><strong>Szlltsi cm:</strong> {orderData.shippingMethod.deliveryDetails.address}, {orderData.shippingMethod.deliveryDetails.city}, {orderData.shippingMethod.deliveryDetails.postalCode}</p>
                    </>
                )}
                {orderData.shippingMethod.lockerLocation && (
                    <p><strong>Csomagpont:</strong> {orderData.shippingMethod.lockerLocation}</p>
                )}
                <strong>Fizetsi md:</strong> {orderData.paymentMethod?.method || 'Nincs kivlasztva'} - {(orderData.paymentMethod?.price || 0).toLocaleString()} Ft

                <h3>Termkek:</h3>
                <ul>
                    {orderData.items?.map((item, index) => (
                        <li key={index}>
                            {item.name || 'Ismeretlen termk'} - {item.quantity} db - sszesen: {(item.price * item.quantity).toLocaleString()} Ft
                        </li>
                    ))}
                </ul>

                <h3>Vgsszeg:</h3>
                <p><strong>{((orderData.totalAmount || 0) + (orderData.shippingMethod?.price || 0) + (orderData.paymentMethod?.price || 0)).toLocaleString()} Ft</strong></p>
            </div>
            
            <div className="review-buttons">
                <button type="button" onClick={onBack}>Vissza</button>
                <button onClick={handleSubmit}>Rendels vglegestse</button>
            </div>
        </div>
    );
};

export default ReviewOrder;
