import React, { useEffect, useState } from 'react';
import './AdminOrdersPage.css';

const AdminOrdersPage = () => {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const [selectedPaymentMethod, setSelectedPaymentMethod] = useState('');
    const [selectedShippingMethod, setSelectedShippingMethod] = useState('');

    useEffect(() => {
        const fetchOrders = async () => {
            try {
                const response = await fetch('http://localhost:5000/api/orders');
                if (!response.ok) {
                    throw new Error('Hiba trtnt a rendelsek lekrsekor.');
                }
                const data = await response.json();
                setOrders(data);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchOrders();
    }, []);

    // Szrt rendelsek
    const filteredOrders = orders.filter((order) => {
        const matchesPayment =
            selectedPaymentMethod === '' || order.paymentMethod === selectedPaymentMethod;
        const matchesShipping =
            selectedShippingMethod === '' || order.shippingMethod === selectedShippingMethod;
        return matchesPayment && matchesShipping;
    });

    if (loading) {
        return <p>Rendelsek betltse...</p>;
    }

    if (error) {
        return <p style={{ color: 'red' }}>{error}</p>;
    }

    return (
        <div className="admin-orders-page">
            <h1>Rendelsek kezelse</h1>

            {/* Szrk */}
            <div className="filters">
                <label>
                    Fizetsi md:
                    <select
                        value={selectedPaymentMethod}
                        onChange={(e) => setSelectedPaymentMethod(e.target.value)}
                    >
                        <option value="">sszes</option>
                        <option value="Bankkrtya">Bankkrtya</option>
                        <option value="Utnvt">Utnvt</option>
                        <option value="banki tutals">Banki tutals</option>
                        {/* Tovbbi lehetsgek */}
                    </select>
                </label>

                <label>
                    Szlltsi md:
                    <select
                        value={selectedShippingMethod}
                        onChange={(e) => setSelectedShippingMethod(e.target.value)}
                    >
                        <option value="">sszes</option>
                        <option value="Futrszolglat">Futrszolglat</option>
                        <option value="szemlyes tvtel">Szemlyes tvtel</option>
                        <option value="csomagautomata">Csomagautomata</option>
                        {/* Tovbbi lehetsgek */}
                    </select>
                </label>
            </div>
            <table>
                <thead>
                    <tr>
                        <th>Rendels ID</th>
                        <th>Vsrl neve</th>
                        <th>Dtum</th>
                        <th>sszeg</th>
                        <th>Fizetsi md</th>
                        <th>Szlltsi md</th>
                        <th>llapot</th>
                    </tr>
                </thead>
                <tbody>
                    {filteredOrders.map((order) => (
                        <tr key={order._id}>
                        <td>{order._id}</td>
                        <td>{order.contactInfo?.name || 'Nincs megadva nv'}</td>
                        <td>{order.createdAt ? new Date(order.createdAt).toLocaleString() : 'Nincs adat'}</td>
                        <td>{order.totalAmount ? `${order.totalAmount.toLocaleString()} Ft` : 'Nincs adat'}</td>
                        <td>{order.paymentMethod || 'Nincs adat'}</td>
                        <td>
                            {order.shippingMethod?.method || 'Nincs adat'}
                            {/* Szlltsi adatok */}
                        </td>
                        <td>{order.status || 'Nincs adat'}</td>
                        </tr>
                    ))}
                </tbody>

            </table>
        </div>
    );
};

export default AdminOrdersPage;
