import React, { useState } from 'react';
import './PaymentMethod.css';

const PaymentMethod = ({ onNext, onBack, selectedMethod }) => {

    const paymentPrices = {
        'Bankkrtya': 0,
        'Utnvt': 490,
    }

    const [method, setMethod] = useState(selectedMethod || '');

    const handleChange = (e) => {
        setMethod(e.target.value);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const paymentPrice = paymentPrices[method]; // Fizetsi r meghatrozsa
    
        onNext({
            method,
            price: paymentPrice,
        });
    };
    
    

    return (
        <form onSubmit={handleSubmit} className="payment-method-container">
            <h2>Fizetsi md kivlasztsa</h2>
            <label>
                <input type="radio" value="Bankkrtya" checked={method === 'Bankkrtya'} onChange={handleChange} />
                Bankkrtya - ingyenes
            </label>
            <label>
                <input type="radio" value="Utnvt" checked={method === 'Utnvt'} onChange={handleChange} />
                Utnvt - {paymentPrices['Utnvt']} Ft
            </label>
            <div className="payment-buttons">
                <button type="button" className="back-button" onClick={onBack}>Vissza</button>
                <button type="submit">Tovbb</button>
            </div>
        </form>
    );
};

export default PaymentMethod;